.contact-container {
  min-height: 100vh;
background-color: black;
  padding: 50px 20px;
  color: white;
  text-align: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.contact-title {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.contact-subtitle {
  font-size: 1.1rem;
  margin-bottom: 40px;
  color: #f0f0f0;
}

.contact-form {
  max-width: 500px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.1);
  padding: 25px;
  border-radius: 10px;
  backdrop-filter: blur(8px);
}

.contact-input,
.contact-textarea {
  width: 100%;
  margin-bottom: 15px;
  padding: 12px;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-family: inherit;
}

.contact-input {
  height: 45px;
}

.contact-textarea {
  height: 120px;
  resize: none;
}

.contact-btn {
  width: 100%;
  padding: 12px;
  font-size: 1.1rem;
  font-weight: bold;
  background-color: #061d7a;
  border: none;
  color: white;
  border-radius: 20px;
  cursor: pointer;
  transition: background 0.3s ease;
  margin-left: 10px;
}

.contact-btn:hover {
  background-color: #f50057;
}

.success-msg {
  margin-top: 20px;
  font-size: 1rem;
  font-weight: bold;
  color: #00e676;
}
